.ui-container {
  position: absolute;
  margin: 0;
  padding: 0;
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

@media only screen and (max-width: 1000px) {
  .chrono {
    position: absolute;
    right: 0;
    bottom: 0;
    margin: 5px
  }
  
  .chrono-finished {
    width: 50%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 5px;
    background-color: white;
    border-radius: 25px;
    padding: 2%
  }
  
  .restart-btn {
    color: white;
    font-size: large;
    background-color: red;
    border: none;
    padding: 4% 8%;
    margin: 10px 0;
    border-radius: 10px;
  }
  
  .tutorial {
    position: absolute;
    left: 25px;
    bottom: 25px;
    background-color: white;
    border-radius: 25px;
    padding: 20px;
  }
  
  h1 {
    margin : 0;
    font-size: 1.5rem;
  }

  h2 {
    margin : 0;
    font-size: 1rem;
  }
  
  .speed {
    position: absolute;
    left: 25px;
    bottom: 25px;
    font-size: 2rem;
  }
  
}

